export default {
  data () {
    return {
      activeIndex: 0,
      menuList: [
        '全部',
        '待付款',
        '待收货',
        '退款/退货'
      ],
      orderList: []
    }
  },
  onLoad (options) {
    this.activeIndex = parseInt(options.activeIndex) || 0
    this.queryOrder()
  },
  methods: {
    clickQuery (index) {
      this.activeIndex = index
      this.queryOrder()
    },
    queryOrder () {
      this.$request({
        url: '/api/public/v1/my/orders/all?type=' + (this.activeIndex + 1),
        isAuth: true
      }).then(data => {
        console.log(data)
        this.orderList = data.orders
      })
    }
  }
}
